import React from 'react'
import Button from '../button'
import * as S from './style'

const ItemCard = ({ color, title, price, video, setOpenModal, children }) => (
  <>
    <S.CardTitle color={color}>
      <h3>{title}</h3>
      <video
        onMouseEnter={event => event.target.play()}
        onMouseLeave={event => event.target.pause()}
        src={video}
        loop
        preload
        muted
      />
    </S.CardTitle>
    <S.ItemBody color={color}>{children}</S.ItemBody>
    <S.CardFooter>
      <Button onClick={() => setOpenModal(true)}>Get Started</Button>
      <h5>{price}</h5>
    </S.CardFooter>
  </>
)

export default ItemCard
