import React, { useEffect } from 'react'
import Center from '../grid/center'
import Button from '../button'
import * as S from './style'

const body = document.querySelector('body').classList

const ItemModal = ({
  openModal,
  setOpenModal,
  title,
  color,
  price,
  linkToBuy,
  children,
}) => {
  useEffect(() => {
    openModal ? body.add('open-modal') : body.remove('open-modal')
  }, [openModal])

  return (
    <S.Modal open={openModal}>
      <S.ModalBackground onClick={() => setOpenModal(false)} />
      <S.ModalPaper>
        <S.ModalTitle color={color}>
          <Center>
            <S.ModalTitleDisplay>
              <h3>{title}</h3>
              <S.ModalCloseButton onClick={() => setOpenModal(false)}>
                close
              </S.ModalCloseButton>
            </S.ModalTitleDisplay>
          </Center>
        </S.ModalTitle>
        <S.ModalContent>
          <Center>
            <S.ItemBody color={color}>{children}</S.ItemBody>
            <S.ModalFooter>
              <Button
                as="a"
                href={linkToBuy}
                target="_blank"
                rel="noopener noreferrer"
              >
                Get it now!
              </Button>
              <h5>{price}</h5>
            </S.ModalFooter>
          </Center>
        </S.ModalContent>
      </S.ModalPaper>
    </S.Modal>
  )
}

export default ItemModal
