import React from 'react'
import { StaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'
import quote from '../../icons/quote.svg'
import * as S from './style'

const Item = ({ author, photo, children }) => {
  return (
    <S.Item>
      <S.ImageContainer>
        <StaticQuery
          query={graphql`
            query {
              images: allFile {
                edges {
                  node {
                    relativePath
                    name
                    childImageSharp {
                      fluid(maxWidth: 540) {
                        ...GatsbyImageSharpFluid
                      }
                    }
                  }
                }
              }
            }
          `}
          render={data => {
            const image = data.images.edges.find(n =>
              n.node.relativePath.includes(photo)
            )
            if (!image) return null

            return <Img alt={author} fluid={image.node.childImageSharp.fluid} />
          }}
        />
      </S.ImageContainer>
      <S.Content>
        <S.Quote as="img" src={quote} />
        {children}
        <h6>{author}</h6>
      </S.Content>
    </S.Item>
  )
}

export default Item
