import React from 'react'
import * as S from './style'

const settings = {
  dots: false,
  speed: 500,
  infinite: false,
  slidesToShow: 1,
  slidesToScroll: 1,
  arrows: false,
  useTransform: false,
}

const List = ({ children }) => <S.List {...settings}>{children}</S.List>

export default List
