import styled from 'styled-components'
import menuIcon from '../../icons/menu-black.svg'
import whiteMenuIcon from '../../icons/menu-white.svg'
import closeIcon from '../../icons/close.svg'
import { mediaQuery } from '../../utils'

export const Header = styled.header`
  background-color: white;
  transition: 0.3s;
  color: black;
  background: white;
  position: fixed;
  color: white;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;

  &.translucent {
    background-color: transparent;

    a {
      color: white;
    }
  }
`

export const Display = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`

export const Logo = styled.img`
  width: 81px;
  padding: 34px 0;
`

export const Nav = styled.nav`
  pointer-events: none;
  position: absolute;
  width: 100%;
  right: 0;
  left: 0;
  top: 0;

  &.show {
    pointer-events: auto;
  }

  ${mediaQuery.large} {
    pointer-events: auto;
    position: relative;
  }
`

export const NavBackground = styled.div`
  background: rgba(0, 0, 0, 0);
  transition: background 0.3s;
  position: fixed;
  width: 100%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;

  &.show {
    background: rgba(0, 0, 0, 0.5);
  }

  ${mediaQuery.large} {
    display: none;
  }
`

export const MobileNavSwitch = styled.span`
  background-image: url(${menuIcon});
  padding: ${({ theme }) => theme.gutter_width.small}px;
  background-size: 18px auto;
  background-repeat: no-repeat;
  background-position: center;
  pointer-events: auto;
  display: block;
  width: 18px;
  height: 18px;
  cursor: pointer;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 2;

  &.close {
    background-image: url(${closeIcon});
  }

  ${mediaQuery.medium} {
    background-image: url(${whiteMenuIcon});

    header:not(.translucent) &:not(.close) {
      background-image: url(${menuIcon});
    }
  }

  ${mediaQuery.large} {
    display: none;
  }
`

export const NavItems = styled.ul`
  transform: translateY(-100%);
  transition: transform 0.3s;
  background: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
  padding: 0;
  margin: 0;
  padding: 45px 0;

  &.show {
    transform: translateY(0);
  }

  a {
    display: inline-block;
    text-decoration: none;
    padding: 25px;
    cursor: pointer;
  }

  ${mediaQuery.large} {
    transition: 0s;
    background: transparent;
    transform: translateY(0);
    flex-direction: row;
    justify-content: flex-end;
    padding: 0;

    header:not(.translucent) & a {
      color: black;
    }
  }
`
